import React from "react";
import Box from "./styled";

import { Radio as ARadio } from "antd";

const ARadioGroup = ARadio.Group;
const ARadioButton = ARadio.Button;

class RadioGroup extends ARadioGroup {
    static displayName = "RadioGroup";

    render() {
        return <ARadioGroup {...this.props}>{this.props.children}</ARadioGroup>;
    }
}

class RadioButton extends ARadioButton {
    render() {
        return <ARadioButton {...this.props}>{this.props.children}</ARadioButton>;
    }
}

/**
 * Form
 */
class Radio extends ARadio {
    static Group = RadioGroup;
    static Button = Box(RadioButton);
}

export default Box(Radio);

