import styled, { css } from "styled-components";
import React from "react";

const BoxStyling = css`
  box-sizing: border-box;
`;

const Zebra = css`
  border-radius:50%;
`;

const Box = (Comp) => {
    const NewBox = styled(Comp)`
    ${(props) => {
        const theme = props.theme;
        if (props.notstyled === "true") {
            return css``;
        }
        
        return css`
              ${BoxStyling}
              ${props.special === "zebra" && Zebra}
              &&, .ant-btn-sm, & {
               height: auto;
              }
          `;
    }};
`;
    return NewBox;
};


export default Box;
