import styled, { css } from "styled-components";
import React from "react";

const Box = (Comp) => {
    const NewBox = styled(Comp)`
    ${(props) => {
        const theme = props.theme;
        return css`
            padding: ${props.size === "large" ? theme.btnPaddingLg : theme.btnPaddingBase};
            && {
                height: auto;
                line-height: normal;
            }
          `;
    }};
`;
    return NewBox;
};


export default Box;
