import React from "react";

import { Input as AInput } from "antd";
// import {InputGroup as AInputGroup} from "antd";
const AInputGroup = AInput.Group;

class InputGroup extends React.Component {
    static displayName = "InputGroup";

    render() {
        return <AInputGroup {...this.props}>{this.props.children}</AInputGroup>;
    }
}

/**
 * Form
 */
class Input extends AInput {
    static Group = InputGroup;
}

export default Input;

