import styled, {css} from "styled-components";
import React, {Component} from "react";
import createTheme from "./generateTheme/createTheme";

const BoxStyling = css`
  box-sizing: border-box;
`;

const Box = (Comp) => {
    const NewBox = styled(Comp)`
    ${(props) => {
        const theme = createTheme(props.theme);
        if (props.notstyled === "true") {
            return css``;
        }

        return css`
              ${BoxStyling}
              //background-color:lightGray !important;
              //border: 1px solid ${theme.primary6};
              height: auto;
              //padding: ${theme.btnPaddingLg}
          `;
    }};
`;
    return NewBox;
};


export default Box;
