import React from "react";
import {
    isMobile
} from "react-device-detect";
import Box from "../theme/Box";

import AvatarW from "./Avatar";


const Avatar = (props) => {
    if (isMobile) {
        // TODO ADD MOBILE SUPPORT
        return <AvatarW {...props}/>;
    } else {
        return <AvatarW {...props}/>;
    }
};

Avatar.displayName = "Avatar";

export default Box(Avatar);
