import React from "react";

import { Form as AForm } from "antd";

const AFormItem = AForm.Item;

class FormItem extends AFormItem {
    static displayName = "FormItem";

    render() {
        return <AFormItem {...this.props}>{this.props.children}</AFormItem>;
    }
}

/**
 * Form
 */
class Form extends AForm {
    static Item = FormItem;
    static create = AForm.create;
}

export default Form;

