import React, { Component } from "react";
import PropTypes from "prop-types";
import {Icon as AIcon} from "antd";

/**
 * Icon
 */
class Icon extends Component {

    render() {
        let passedProps = Object.assign({}, this.props);
        let size = passedProps.size || false;
        size = size.indexOf("px") !== -1 ? size : Icon.sizeMap[size];
        if (size !== "default") {
            let newStyle = Object.assign({}, passedProps.style, { fontSize: size });
            passedProps.style = newStyle;
        }
        Object.keys(passedProps).forEach((key) => (passedProps[key] == null || passedProps[key] === "") && delete passedProps[key]);
        return <AIcon {...passedProps} />;
    }
}

