Icon.sizes = [
    "default",
    "medium",
    "large",
    "x-large"
];
Icon.sizeMap = {
    "default": 16,
    "medium": 32,
    "large": 48,
    "x-large": 64
};

Icon.sizeMapByNumber = {
    16: "default",
    32: "medium",
    48: "large",
    64: "x-large"
};



Icon.Icons = { outlined: icons, filled: IconsFilled, twoTone: IconsTwoTone };

Icon.propTypes = {
    // type: PropTypes.oneOf(icons),
    theme: PropTypes.oneOf(Icon.themes),
    style: PropTypes.object,
    spin: PropTypes.bool,
    size: PropTypes.oneOfType([PropTypes.oneOf(Icon.sizes), PropTypes.string]),
    type: function(props, propName, componentName) {
        if (props["theme"] === "outlined") {
            return null;
        }
        if (props["theme"] === "twoTone" && IconsTwoTone.indexOf(props[propName]) === -1) {
            return new Error(`${propName} with value ${props[propName]} ${componentName} Validation failed.`);
        } else if (props["theme"] === "filled" && IconsFilled.indexOf(props[propName]) === -1) {
            return new Error(`${propName} with value ${props[propName]} ${componentName} Validation failed.`);
        }
    },

    component: PropTypes.node,
    twoToneColor: PropTypes.string
};

Icon.defaultProps = {
    type: icons[52],
    theme: "outlined",
    style: {},
    size: "default",
    spin: false,
    component: null,
    twoToneColor: ""
};

Icon.displayName = "Icon";
export default Icon;
