import React from "react";
import {
    isMobile
} from "react-device-detect";
import Box from "../theme/Box";

import DividerW from "./Divider";

const Divider = (props) => {
    if (isMobile) {
        // TODO ADD MOBILE
        return <DividerW {...props} />;
    } else {
        return <DividerW {...props} />;
    }
};

Divider.displayName = "Divider";

export default Box(Divider);
